.list {
    list-style-type: none;
    margin: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 8px 0 16px;
    margin-bottom: 40px;
    gap: 24px;
    box-sizing: border-box;
}

.item {
    margin: 0;
    padding: 0;
}